<template>
    <el-card class="card" shadow="hover">
        <el-row :gutter="20">
            <el-col :span="16">
                <el-checkbox v-model="isCompleted" size="large">
                    <div id="taskNameAndDuration" :class="{ completed: this.isCompleted }">{{ task.taskName }} - {{ task.duration }}分钟
                    </div>
                </el-checkbox>
            </el-col>
            <el-col :span="4">
                <el-button class="centered-button" type="primary" @click="openEditTask(this.task)">
                    <el-icon>
                        <Edit />
                    </el-icon>
                </el-button>

            </el-col>
            <el-col :span="4">
                <el-button class="centered-button" type="danger" @click="deleteTask(this.task)">
                    <el-icon>
                        <Delete />
                    </el-icon>
                </el-button>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="8" :offset="16">
                <div class="date-info">
                    <span>添加时间: {{ new Date(this.task.addTaskTime).toLocaleString() }}</span>
                    <br>
                    <span v-if="this.task.isCompleted">完成时间: {{ new Date(this.task.completeTaskTime).toLocaleString()
                        }}</span>
                </div>
            </el-col>
        </el-row>
    </el-card>
</template>
<script>
import { Edit, Delete } from '@element-plus/icons-vue';
export default {
    components: {
        Edit,
        Delete,
    },
    data() {
        return {
            isCompleted: this.task.isCompleted,
        }
    },
    watch: {
        isCompleted() {
            this.$emit('complete-task', this.task, this.isCompleted);
        }
    },
    props: {
        task: Object,
    },
    methods: {
        openEditTask(task) {
            this.$emit('open-edit-task', task);
        },
        deleteTask(task) {
            this.$emit('delete-task', task);
        },
    },
}
</script>

<style>
.centered-button {
    text-align: center;
}

.completed {
    text-decoration: line-through;
}
#taskNameAndDuration {
    font-family: 'Arial';
    font-size: 24px;
}
.card {
    height: 120px;
    margin-bottom: 5px;
}
.date-info {
    margin-top: 5px;
    margin-bottom: 5px;
    font-size: 12px;
}
</style>