<template>
    <div class="input-group">
        <el-select v-model="taskGroup" filterable allow-create default-first-option placeholder="选择/输入任务组">
            <el-option v-for="(value,key) in tasks" :key="key" :label="key" :value="key">
            </el-option>
        </el-select>
        <el-input v-model="taskName" placeholder="输入任务名" />
        <el-input @keyup.enter="addTask" v-model.number="duration" type="number" placeholder="输入任务持续时间" />
        <el-button type="primary" @click="addTask">添加任务</el-button>
    </div>
</template>
<script>
import { ElMessage } from 'element-plus';
export default {
    data() {
        return {
            taskGroup: '',
            taskName: '',
            duration: 0,
            isCompleted: false,
            addTaskTime: 0,
            completeTaskTime: 0,
        }
    },
    props: {
        tasks: Object,
    },
    methods: {
        addTask() {
            let newTask = {
                taskId: Math.random().toString(36).substring(2, 10),
                taskName: this.taskName,
                duration: this.duration,
                isCompleted: this.isCompleted,
                addTaskTime: new Date().getTime(),
                completeTaskTime: 0,
            }
            if (this.tasks[this.taskGroup]) {
                for (let task of this.tasks[this.taskGroup]) {
                    if (task.taskName === this.taskName) {
                        ElMessage({
                            message: '任务名已存在',
                            type: 'error'
                        });
                        return;
                    }
                }
            }
            this.$emit('add-task', this.taskGroup, newTask);
            ElMessage({
                message: '添加成功',
                type: 'success'
            });
        },
    },
}
</script>
<style>
.input-group {
    display: flex;
    gap: 10px;
    margin-bottom: 10px;
}
</style>