<template>
    <el-card class="summary" shadow="hover">
        <el-row>
            <el-col :span="12">
                <el-statistic :value="this.taskNumber" title="总任务数" />
            </el-col>
            <el-col :span="12">
                <el-statistic :value="this.allTime" title="总时间" suffix="分钟" />
            </el-col>
            <el-col :span="12">
                <el-statistic :value="this.completedTaskNumber" title="已完成任务数" />
            </el-col>
            <el-col :span="12">
                <el-statistic :value="this.completedTime" title="已完成时间" suffix="分钟" />
            </el-col>
            <el-col :span="12">
                <el-statistic :value="this.showFirstTaskAddTime" title="首次添加任务时间" />
            </el-col>
            <el-col :span="12">
                <el-statistic :value="this.predictFinishTime" title="预计完成时间" />
            </el-col>
        </el-row>
        <div class="progress">
            <el-progress :text-inside="true" :percentage="this.completedPercentage" :stroke-width="20" />
        </div>
    </el-card>
</template>
<script>
export default {
    props: {
        tasks: Object,
    },
    data() {
        return {
            taskNumber: 0,
            allTime: 0,
            completedTaskNumber: 0,
            completedTime: 0,
            completedPercentage: 0,
            firstTaskAddTime: '',
            showFirstTaskAddTime: '',
            predictFinishTime: '',
        }
    },
    watch: {
        tasks: {
            handler: 'calculateTaskSummary',
            deep: true,
            immediate: true,
        },
    },
    methods: {
        calculateTaskSummary() {
            let taskNumber = 0;
            let allTime = 0;
            let completedTaskNumber = 0;
            let completedTime = 0;
            if (Array.isArray(this.tasks)) {
                for (let task of this.tasks) {
                    taskNumber++;
                    allTime += task.duration;
                    if (task.isCompleted) {
                        completedTaskNumber++;
                        completedTime += task.duration;
                    }
                    if(!this.firstTaskAddTime || task.addTaskTime < this.firstTaskAddTime) {
                        this.firstTaskAddTime = task.addTaskTime;
                    }
                }
            }
            else {
                for (let group in this.tasks) {
                    for (let task of this.tasks[group]) {
                        taskNumber++;
                        allTime += task.duration;
                        if (task.isCompleted) {
                            completedTaskNumber++;
                            completedTime += task.duration;
                        }
                        if(!this.firstTaskAddTime || task.addTaskTime < this.firstTaskAddTime) {
                            this.firstTaskAddTime = task.addTaskTime;
                        }
                    }
                }
            }
            let completedPercentage = completedTime / allTime * 100;
            completedPercentage = Number.parseFloat(completedPercentage.toFixed(2));
            this.taskNumber = taskNumber;
            this.allTime = allTime;
            this.completedTaskNumber = completedTaskNumber;
            this.completedTime = completedTime;
            this.completedPercentage = completedPercentage;
            this.showFirstTaskAddTime = new Date(this.firstTaskAddTime).toLocaleString();
            this.predictFinishTime = this.calculatePredictFinishTime();
        },
        calculatePredictFinishTime() {
            const now = new Date().getTime();
            const predictFinishTime = now + (this.allTime - this.completedTime) / this.completedTime * (now - this.firstTaskAddTime);
            return new Date(predictFinishTime).toLocaleString();
        },
    },
}
</script>
<style>
.summary {
    margin-top: 20px;
    margin-bottom: 10px;
}

.progress .el-progress--line {
    margin-top: 10px;
    max-width: 600px;
}
</style>