<template>
    <div class="container">
        <h1>任务管理 App</h1>
        <el-row>
            <el-col :span="6">
                <el-button type="primary" @click="sortTasksByName">
                    按任务名排序
                </el-button>
            </el-col>
            <el-col :span="6">
                <el-button type="primary" @click="sortTasksByAddTime">
                    按添加时间排序
                </el-button>
            </el-col>
            <el-col :span="6">
                <el-button type="primary" @click="exportTasks">
                    导出任务
                    <el-icon>
                        <Download />
                    </el-icon>
                </el-button>
            </el-col>
            <el-col :span="6">
                <el-upload action="#" :auto-upload="false" :on-change="importTasks">
                    <el-button type="primary">
                        导入任务
                        <el-icon>
                            <DocumentAdd />
                        </el-icon>
                    </el-button>
                </el-upload>
            </el-col>
        </el-row>
        <el-button type="danger" @click="dialogVisible = true">清除已完成任务</el-button>
        <el-dialog v-model="dialogVisible" title="提示" width="500">
            <span>确定清除已完成任务？</span>
            <template #footer>
                <div class="dialog-footer">
                    <el-button @click="dialogVisible = false">取消</el-button>
                    <el-button type="primary" @click="clearCompleted">
                        确定
                    </el-button>
                </div>
            </template>
        </el-dialog>
        <AddTask :tasks="tasks" @add-task="addTask"></AddTask>
        <el-collapse accordion>
            <el-collapse-item v-for="(groupTasks, groupIndex) in tasks" :key="groupIndex" :title="groupIndex">
                <el-scrollbar height="400px">
                    <TaskInfo v-for="(task, index) in groupTasks" :key="index" :task="task"
                        @complete-task="completeTask" @delete-task="deleteTask" @open-edit-task="openEditTask">
                    </TaskInfo>
                </el-scrollbar>

                <TaskSummary :tasks="groupTasks"></TaskSummary>
                <EditTask v-model="editTaskVisible" :tasks="tasks" :editTaskId="editTaskId"
                    :editTaskVisible="editTaskVisible" @edit-task="editTask"></EditTask>
            </el-collapse-item>
        </el-collapse>
        <TaskSummary :tasks="tasks"></TaskSummary>
        <TaskStat :tasks="tasks"></TaskStat>
    </div>
</template>

<script>
import TaskSummary from './components/TaskSummary.vue';
import TaskInfo from './components/TaskInfo.vue';
import AddTask from './components/AddTask.vue';
import EditTask from './components/EditTask.vue';
import TaskStat from './components/TaskStat.vue';
import { Download, DocumentAdd } from '@element-plus/icons-vue';

export default {
    name: 'App',
    components: {
        TaskSummary,
        TaskInfo,
        AddTask,
        EditTask,
        TaskStat,
        Download,
        DocumentAdd,
    },
    data() {
        return {
            dialogVisible: false,
            editTaskVisible: false,
            editTaskId: '',
            tasks: {
            },
        }
    },
    computed: {
    },
    mounted() {
        const tasks = localStorage.getItem('tasks');
        if (tasks) {
            this.tasks = JSON.parse(tasks);
        }
    },
    methods: {
        sortTasksByName() {
            for (let group in this.tasks) {
                this.tasks[group].sort((a, b) => {

                    // 使用正则表达式提取 taskName 中的数字部分
                    const numberA = parseInt(a.taskName.match(/\d+/)[0]);
                    const numberB = parseInt(b.taskName.match(/\d+/)[0]);

                    // 比较两个数字
                    if (numberA < numberB) {
                        return -1;
                    } else if (numberA > numberB) {
                        return 1;
                    } else {
                        // 如果数字相同，使用原始的字符串比较方法
                        return a.taskName.localeCompare(b.taskName);
                    }
                });
            }
            localStorage.setItem('tasks', JSON.stringify(this.tasks));
        },
        sortTasksByAddTime() {
            for (let group in this.tasks) {
                this.tasks[group].sort((a, b) => a.addTaskTime - b.addTaskTime);
            }
            localStorage.setItem('tasks', JSON.stringify(this.tasks));
        },
        addTask(taskGroup, newTask) {
            // eslint-disable-next-line no-prototype-builtins
            if (this.tasks.hasOwnProperty(taskGroup)) {
                this.tasks[taskGroup].push(newTask);
            } else {
                this.tasks[taskGroup] = [newTask];
            }
            localStorage.setItem('tasks', JSON.stringify(this.tasks));
        },
        openEditTask(task) {
            this.editTaskId = task.taskId;
            this.editTaskVisible = true;
        },
        editTask(taskGroup, editedTask) {
            for (let group in this.tasks) {
                for (let i = 0; i < this.tasks[group].length; i++) {
                    if (this.tasks[group][i].taskId === editedTask.taskId) {
                        if (group === taskGroup) {
                            this.tasks[group][i] = editedTask;
                        } else {
                            this.tasks[group] = this.tasks[group].filter(t => t.taskId !== editedTask.taskId);
                            if (this.tasks[group].length === 0) {
                                delete this.tasks[group];
                            }
                            // eslint-disable-next-line no-prototype-builtins
                            if (this.tasks.hasOwnProperty(taskGroup)) {
                                this.tasks[taskGroup].push(editedTask);
                            } else {
                                this.tasks[taskGroup] = [editedTask];
                            }
                        }
                    }
                }
                localStorage.setItem('tasks', JSON.stringify(this.tasks));
            }
            this.editTaskVisible = false;
        },
        changeVisible() {
            this.dialogVisible = !this.dialogVisible;
        },
        deleteTask(task) {
            for (let group in this.tasks) {
                this.tasks[group] = this.tasks[group].filter(t => t.taskId !== task.taskId);
            }
            for (let group in this.tasks) {
                if (this.tasks[group].length === 0) {
                    delete this.tasks[group];
                }
            }
            localStorage.setItem('tasks', JSON.stringify(this.tasks));
        },
        completeTask(task, isCompleted) {
            for (let group in this.tasks) {
                this.tasks[group] = this.tasks[group].map(t => {
                    if (t.taskId === task.taskId) {
                        t.isCompleted = isCompleted;
                        if (t.isCompleted) {
                            t.completeTaskTime = new Date().getTime();
                        } else {
                            t.completeTaskTime = 0;
                        }
                    }
                    return t;
                });
            }
            localStorage.setItem('tasks', JSON.stringify(this.tasks));
        },
        clearCompleted() {
            this.changeVisible();
            for (let group in this.tasks) {
                this.tasks[group] = this.tasks[group].filter(task => !task.isCompleted);
            }
            for (let group in this.tasks) {
                if (this.tasks[group].length === 0) {
                    delete this.tasks[group];
                }
            }
            localStorage.setItem('tasks', JSON.stringify(this.tasks));
        },
        exportTasks() {
            const jsonData = JSON.stringify(this.tasks);
            const blob = new Blob([jsonData], { type: 'application/json' });
            const url = URL.createObjectURL(blob);
            const a = document.createElement('a');
            a.href = url;
            a.download = 'tasks.json';
            document.body.appendChild(a);
            a.click();
            document.body.removeChild(a);
        },
        importTasks(file) {
            if (file) {
                const reader = new FileReader();
                reader.onload = () => {
                    const data = JSON.parse(reader.result);
                    this.tasks = data;
                    localStorage.setItem('tasks', JSON.stringify(this.tasks));
                };
                reader.readAsText(file.raw);
            }
        },
    },
}
</script>

<style scoped>
.el-collapse>>>.el-collapse-item__header {
    font-size: 30px;
    font-weight: bold;
}

.container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
}
</style>