<template>
    <el-dialog title="编辑任务" class="input-group" width="500">
        <el-input v-model="taskGroup" placeholder="输入任务组" />
        <el-input v-model="taskName" placeholder="输入待完成任务" />
        <el-input @keyup.enter="editTask" v-model.number="duration" type="number" placeholder="输入任务持续时间" />
        <el-button type="primary" @click="editTask">编辑任务</el-button>
    </el-dialog>
</template>
<script>
import { ElMessage } from 'element-plus';
export default {
    data() {
        return {
            taskGroup: '',
            taskName: '',
            duration: 0,
            isCompleted: false,
            addTaskTime: 0,
            completeTaskTime: 0,
        }
    },
    props: {
        tasks: Object,
        editTaskId: String,
        editTaskVisible: Boolean,
    },
    watch: {
        editTaskVisible() {
            if(this.editTaskVisible){
                this.getTask();
            }
        }
    },
    methods: {
        getTask(){
            for(let group in this.tasks){
                for(let task of this.tasks[group]){
                    if(task.taskId === this.editTaskId){
                        this.taskGroup = group;
                        this.taskName = task.taskName;
                        this.duration = task.duration;
                        this.isCompleted = task.isCompleted;
                        this.addTaskTime = task.addTaskTime;
                        this.completeTaskTime = task.completeTaskTime;
                    }
                }
            }
        },
        
        editTask() {
            let editedTask = {
                taskId: this.editTaskId,
                taskName: this.taskName,
                duration: this.duration,
                isCompleted: this.isCompleted,
                addTaskTime: this.addTaskTime,
                completeTaskTime: this.completeTaskTime,
            }
            this.$emit('edit-task', this.taskGroup, editedTask);
            ElMessage({
                message: '编辑成功',
                type: 'success'
            });
        },
    },
}
</script>
<style>
.input-group {
    display: flex;
    gap: 10px;
    margin-bottom: 10px;
}
</style>